.home-parallax {
  height: 100vh;
  width: 100%;
  overflow: hidden;
  position: relative;
  text-align: center;
}

.home-parallax .home-parallax-content {
  position: absolute;
  width: 100%;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  color: #fff;
}

.home-parallax .home-parallax-content h1 {
  font-size: 0.6rem;
  margin: 0;
}

.home-parallax .home-parallax-content p {
  font-size: 0.4rem;
}

.introduction {
  color: #fff;
  font-size: 0.24rem;
}.water-fall {
  margin-top: 0.4rem;
}

.water-fall:after {
  content: '';
  clear: both;
}

.water-fall-list {
  float: left;
}.member {
  width: 100%;
  position: relative;
  overflow: hidden;
}

.member.hover .member-hover-layer {
  opacity: 1;
}

.member-hover-layer {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  opacity: 0;
  background-color: rgba(0,0,0,0.6);
  -webkit-transition: all 0.5s ease;
  transition: all 0.5s ease;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
}

.member-photo {
  width: 100%;
  display: block;
  -webkit-transition: all 0.5s ease;
  transition: all 0.5s ease;
}

.member-hover-photo {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  width: 100%;
}

.member-footer {
  -webkit-transition: height 0.4s ease;
  transition: height 0.4s ease;
  height: 0;
  overflow: hidden;
}

.member-love {
  position: absolute;
  right: 0.1rem;
  bottom: 0.1rem;
  width: 0.2rem;
  height: 0.2rem;
  color: #fff;
  cursor: pointer;
}

.member-love.fill {
  color: #f00;
}

.member-love svg {
  width: 100%;
  height: 100%;
}.heart-c {
  width: 0.4rem;
  height: 0.4rem;
  cursor: pointer;
}
.fa-icon {
  display: inline-block;
  fill: currentColor;
}
.fa-flip-horizontal {
  -webkit-transform: scale(-1, 1);
          transform: scale(-1, 1);
}
.fa-flip-vertical {
  -webkit-transform: scale(1, -1);
          transform: scale(1, -1);
}
.fa-spin {
  -webkit-animation: fa-spin 1s 0s infinite linear;
          animation: fa-spin 1s 0s infinite linear;
}
.fa-inverse {
  color: #fff;
}
.fa-pulse {
  -webkit-animation: fa-spin 1s infinite steps(8);
          animation: fa-spin 1s infinite steps(8);
}
@-webkit-keyframes fa-spin {
0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
}
100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
}
}
@keyframes fa-spin {
0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
}
100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
}
}
#app {
  width: 100%;
}

::-moz-selection {
  background: none;
}

::selection {
  background: none;
}